import React from "react";
import Carousel from "./Carousel";
import Typography from "@material-ui/core/Typography";
import Prismic from "prismic-javascript";
import Map from "./Map";
import Grid from "@material-ui/core/Grid";

export default function Gallery() {
  const [doc, setDocData] = React.useState(null);

  React.useEffect(() => {
    const fetchData = async () => {
      const apiEndpoint = "https://gallery-website.cdn.prismic.io/api/v2";
      const Client = Prismic.client(apiEndpoint);
      const response = await Client.query(
        Prismic.Predicates.at("document.type", "gallery")
      );
      if (response) {
        setDocData(response.results[0].data);
      }
    };
    fetchData();
  }, []);

  return (
    <div>
      <Grid container justify="center">
        <Grid
          item
          xs={12}
          sm={10}
          lg={8}
          xl={6}
          style={{ textAlign: "center", position: "relative" }}
        >
          {console.log(doc)}
          <Carousel />
          <Typography variant="h4" style={{ margin: "40px 0 0" }}>
            {doc && doc.maintitle[0].text}
          </Typography>
          <Typography
            variant="body1"
            style={{ display: "block", margin: "0 10%" }}
          >
            {doc && doc.info[0].text}
          </Typography>
          <Typography
            variant="body1"
            style={{ display: "block", margin: "0 8vw" }}
          >
            {doc && "ADDRESS"}
          </Typography>
          <Typography variant="body1">{doc && doc.address[0].text}</Typography>
          {doc && <Map />}
        </Grid>
      </Grid>
    </div>
  );
}
