import React from "react";
import Prismic from "prismic-javascript";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import ArrowBack from "@material-ui/icons/ArrowBack";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";


export default function Artists() {
  const [doc, setDocData] = React.useState(null);
  const [paintings, setPaintings] = React.useState(null);
  const [currentArtist, setCurrentArtist] = React.useState(null);
  const [open, setOpen] = React.useState(false);
  const [dialogPainting, setDialogPainting] = React.useState(null);

  React.useEffect(() => {
    const fetchData = async () => {
      const apiEndpoint = "https://gallery-website.cdn.prismic.io/api/v2";
      const Client = Prismic.client(apiEndpoint);
      const response = await Client.query(
        Prismic.Predicates.at("document.type", "artistpage")
      );
      if (response) {
        setDocData(response.results);
      }
    };
    fetchData();
  }, []);

  React.useEffect(() => {
    const fetchData = async () => {
      const apiEndpoint = "https://gallery-website.cdn.prismic.io/api/v2";
      const Client = Prismic.client(apiEndpoint);
      const response = await Client.query(
        Prismic.Predicates.at("document.type", "paintings")
      );
      if (response) {
        setPaintings(response.results);
      };
    };
    fetchData();
  }, []);

  const openPage = async art => {
   setCurrentArtist(art);
  };

  const toggleDialog = () => {
    setOpen(!open);
  };

  const openDialog = painting => {
    setDialogPainting(painting);
    toggleDialog();
  };

  const allArtists = () => {
    setCurrentArtist(null)
  }

 

  return (
    <>
      <Grid container>
        {currentArtist ? (
          <Grid item xs={12}>
            <div
              style={{ margin: "auto", padding: "0 10%", maxWidth: "800px" }}
            >
              <Typography
                variant="h4"
                style={{ textAlign: "center", padding: "1vw" }}
              >
                {currentArtist.data.name[0].text}
              </Typography>

              <img
                src={currentArtist.data.photo.url}
                alt=""
                style={{
                  objectFit: "contain",
                  height: "150px",
                  float: "left",
                  margin: "0 1vw"
                }}
              />

              <Typography variant="body2">
                {currentArtist.data.bio.map(p => p.text)}
              </Typography>
              <div></div>
              <Button
                variant="contained"
                color="secondary"
                startIcon={<ArrowBack />}
                onClick={allArtists}
              >
                All Artists
              </Button>
            </div>
          </Grid>
        ) : (doc &&
          doc.map(art => (
            <Grid item xs={12} sm={6} lg={4} key={art.data.name[0].text}>
              <div
                style={{
                  margin: "10%",
                  border: "thin solid black",
                  textAlign: "center",
                  borderRadius: "1vw",
                  overflow: "hidden",
                  color: "white",
                  backgroundColor: "black"
                }}
                onClick={() => openPage(art)}
              >
                <img
                  src={art.data.painting.url}
                  alt=""
                  style={{ objectFit: "contain", width: "100%" }}
                />
                <Typography variant="body1">{art.data.name[0].text}</Typography>
              </div>
            </Grid>
          ))
        )}
      </Grid>

      <Grid container style={{ alignItems: "center",justifyContent:'center', maxWidth: '1000px', margin: 'auto' }}>
        {currentArtist && paintings.map(painting => (
           painting.data.artist[0].text === currentArtist.data.artist[0].text &&
            <Grid
              item
              xs={11}
              sm={6}
              md={4}
              lg={3}
              key={painting.data.image.url}
            >
              <div
                onClick={() => openDialog(painting)}
                style={{
                  marginBottom: "0",
                  margin: "2% auto",
                  textAlign: "center",
                  borderRadius: ".5vw"
                }}
              >
                <img
                  src={painting.data.image.url}
                  alt=""
                  style={{
                    objectFit: "contain",
                    width: "100%",
                    maxHeight: "250px"
                  }}
                />
              </div>
            </Grid>
          ))}
      </Grid>
      <Dialog fullScreen open={open} onClose={toggleDialog}>
        <IconButton
          edge="start"
          color="inherit"
          onClick={toggleDialog}
          aria-label="close"
        >
          <CloseIcon />
        </IconButton>
        {dialogPainting && (
          <div style={{ margin: "0 0", maxHeight: "60%", textAlign: "center" }}>
            <Typography variant="h6" style={{ padding: "2vw" }}>
              {dialogPainting.data.title[0].text}
            </Typography>
            <img
              src={dialogPainting.data.image.url}
              alt=""
              style={{
                objectFit: "contain",
                width: "100%",
                maxHeight: "100%"
              }}
            />
            <Typography
              variant="caption"
              style={{ padding: "2vw", justifySelf: "flex-end" }}
            >
              {dialogPainting.data.artist[0].text}
            </Typography>
            <Typography
              variant="body1"
              style={{ padding: "2vw", justifySelf: "flex-end" }}
            >
              {dialogPainting.data.details[0].text}
            </Typography>
          </div>
        )}
      </Dialog>
    </>
  );
}
