import React,{useContext} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import {StateContext} from './context/context.state';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
    top: 'auto',
    bottom: 0,
    position: 'fixed',
    width: '100%'
  },
});

export default function Footer() {
  const classes = useStyles();
  const [currentTab, setCurrentTab] = React.useState(0);
  const changeTab = (event, num) => {
    setCurrentTab(num)
  };

  return (

    <Paper className={classes.root} >
      <hr  style={{margin: '0'}}/> 
     
      <Tabs 
        value={currentTab}
        onChange={changeTab}
        indicatorColor="primary"
        textColor="primary"
        centered
      >
       
          <Link to='/' onClick={()=> setCurrentTab(0)} style={{textDecoration: 'none'}}>
        <Tab label="Gallery" />
        </Link>
        <Link to='/artists' onClick={()=> setCurrentTab(1)} style={{textDecoration: 'none'}}>
        <Tab label="Artists" />
        </Link>
        <Link to='/originals' onClick={()=> setCurrentTab(2)} style={{textDecoration: 'none'}}>
        <Tab label="Originals" />
        </Link>
       
      </Tabs>
    </Paper>
  );
}