import React from "react";
import Prismic from "prismic-javascript";
import Grid from "@material-ui/core/Grid";
import Dialog from "@material-ui/core/Dialog";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Typography from "@material-ui/core/Typography";

export default function Originals() {
  const [doc, setDocData] = React.useState(null);
  const [open, setOpen] = React.useState(false);
  const [dialogPainting, setDialogPainting] = React.useState(null);

  const toggleDialog = () => {
    setOpen(!open);
  };

  const openDialog = painting => {
    setDialogPainting(painting);
    toggleDialog();
  };

  React.useEffect(() => {
    const fetchData = async () => {
      const apiEndpoint = "https://gallery-website.cdn.prismic.io/api/v2";
      const Client = Prismic.client(apiEndpoint);
      const response = await Client.query(
        Prismic.Predicates.at("document.type", "paintings"), // Order by last publication date from most recent to oldest
        { orderings: "[document.last_publication_date desc]" }
      );
      if (response) {
        setDocData(response.results);
      }
    };
    fetchData();
  }, []);

  return (
    <>
      <Grid container style={{ alignItems: 'center', maxWidth: '1000px', margin: 'auto'}}>
        {doc &&
          doc.map(painting => (
            <Grid
              item
              xs={6}
              sm={4}
              md={3}
              key={painting.data.image.url}
            >
              <div
                onClick={() => openDialog(painting)}
                style={{
                  marginBottom: "0",
                  margin: "2%",
                  textAlign: "center",
                  borderRadius: ".5vw"
                }}
              >
                <img
                  src={painting.data.image.url}
                  alt=""
                  style={{
                    objectFit: "contain",
                    width: "100%",
                    maxHeight: "250px"
                  }}
                />
              </div>
            </Grid>
          ))}
      </Grid>
      <Dialog fullScreen open={open} onClose={toggleDialog}>
        <IconButton
          edge="start"
          color="inherit"
          onClick={toggleDialog}
          aria-label="close"
        >
          <CloseIcon />
        </IconButton>
        {dialogPainting && 
         
          <div style={{ margin: "0 0", maxHeight: "60%",textAlign: 'center' }}>
             <Typography variant='h6'style={{padding: '2vw'}}>
            {dialogPainting.data.title[0].text}
          </Typography>
            <img
              src={dialogPainting.data.image.url}
              alt=""
              style={{
                objectFit: "contain",
                width: "100%",
                maxHeight: "100%"
              }}
            />
             <Typography variant='caption'style={{padding: '2vw',justifySelf: 'flex-end'}}>
            {dialogPainting.data.artist[0].text}
          </Typography>
          <Typography variant='body1'style={{padding: '2vw',justifySelf: 'flex-end'}}>
            {dialogPainting.data.details[0].text}
          </Typography>
          </div>
        }
      </Dialog>
    </>
  );
}
